'use client';

import React, { useState, useTransition } from 'react';
import Link from 'next/link';
import { 
  Sparkles, 
  Brain, 
  Wand2, 
  ImageIcon, 
  Mic, 
  FileEdit, 
  CheckSquare, 
  Lightbulb, 
  Paperclip, 
  ChevronLeft 
} from 'lucide-react';

import CreateQuestionSidebar from '@/components/questions/CreateQuestionSidebar';
import { Difficulty, Topic } from '@/lib/definitions';
import { extractQuestionFromText } from './actions'; // Import Server Action

const CreateQuestionWithAIPage: React.FC = () => {
  // State cho input thô và trạng thái loading
  const [rawInput, setRawInput] = useState('');
  const [isExtracting, startTransition] = useTransition();

  // State cho các trường của câu hỏi
  const [questionText, setQuestionText] = useState('');
  const [options, setOptions] = useState<string[]>(['', '', '', '']);
  const [correctIndex, setCorrectIndex] = useState(0);
  const [explanation, setExplanation] = useState('');
  
  // State cho các trường phân loại
  const [difficulty, setDifficulty] = useState<string>(Difficulty.EASY);
  const [topic, setTopic] = useState<string>(Topic.VOCABULARY);
  const [tags, setTags] = useState<string[]>(['CEFR-B1']);

  const handleExtract = async () => {
    if (!rawInput.trim()) return;

    startTransition(async () => {
      const result = await extractQuestionFromText(rawInput);

      if (result.success && result.data) {
        const { data } = result;
        // Cập nhật state với dữ liệu từ Server Action
        setQuestionText(data.questionText || '');
        setOptions(data.options || ['', '', '', '']);
        setCorrectIndex(data.correctOptionIndex !== undefined ? data.correctOptionIndex : 0);
        setExplanation(data.explanation || '');
        setDifficulty(data.difficulty || Difficulty.EASY);
        setTopic(data.topic || Topic.VOCABULARY);
        setTags(data.tags || []);
      } else {
        // Hiển thị thông báo lỗi cho người dùng
        alert(`Đã xảy ra lỗi khi trích xuất: ${result.error}`);
      }
    });
  };

  const updateOption = (idx: number, val: string) => {
    const newOpts = [...options];
    newOpts[idx] = val;
    setOptions(newOpts);
  };

  const handleSave = () => {
    // TODO: Triển khai logic lưu câu hỏi (sử dụng một Server Action khác)
    console.log("Đã nhấn Lưu:", { questionText, options, correctIndex, explanation, difficulty, topic, tags });
    alert("Câu hỏi đã được lưu thành công vào ngân hàng! ( giả lập ) ");
  };

  return (
    <div className="max-w-7xl mx-auto w-full px-4 md:px-6 lg:px-8 py-8">
      {/* Breadcrumbs và Tiêu đề */}
      <div className="mb-8">
        <Link href="/dashboard/teacher/questions" className="flex items-center gap-2 text-sm text-gray-500 hover:text-[#2b8cee] transition-colors mb-3 w-fit">
            <ChevronLeft size={18} />
            Quay lại danh sách câu hỏi
        </Link>
        <div className="flex flex-col md:flex-row md:items-end justify-between gap-4">
          <div className="flex flex-col gap-2">
            <div className="flex items-center gap-2">
              <span className="px-2.5 py-1 rounded-md bg-[#2b8cee]/10 text-[#2b8cee] text-xs font-bold uppercase tracking-wider">AI Powered</span>
              <h1 className="text-3xl lg:text-4xl font-black text-gray-900 leading-tight tracking-tight">Tạo Câu Hỏi Thông Minh</h1>
            </div>
            <p className="text-gray-500 text-base">Sử dụng AI để tự động trích xuất nội dung câu hỏi từ văn bản thô.</p>
          </div>
        </div>
      </div>

      {/* Vùng Nhập liệu cho AI */}
      <section className="w-full mb-8">
        <div className="bg-white rounded-2xl p-8 shadow-lg border-2 border-[#2b8cee]/20 relative overflow-hidden transition-all duration-300">
          <div className="absolute -top-10 -right-10 opacity-5 pointer-events-none">
            <Sparkles size={200} className="text-[#2b8cee]" />
          </div>
          <div className="relative z-10 flex flex-col gap-6">
            <div className="flex items-center gap-4">
              <div className="p-3 rounded-lg bg-gradient-to-br from-[#2b8cee] to-blue-400 text-white shadow-md">
                <Brain size={28} />
              </div>
              <div>
                <h2 className="text-xl font-bold text-gray-900">Tạo nhanh với AI</h2>
                <p className="text-sm text-gray-500">Dán nội dung thô (từ Word, PDF, Website...) vào đây để AI phân tích.</p>
              </div>
            </div>
            
            <textarea 
              value={rawInput}
              onChange={(e) => setRawInput(e.target.value)}
              className="w-full rounded-xl border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[160px] p-6 text-base placeholder:text-gray-400 transition-all"
              placeholder="Ví dụ: Question: What is the capital of France? A. London B. Paris C. Rome D. Berlin. Correct: B. Explanation: Paris is the capital city of France."
            ></textarea>

            <div className="flex flex-wrap items-center justify-between gap-4">
              <div className="flex items-center gap-2 text-sm text-gray-500">
                <Paperclip size={16} />
                <span>Đính kèm hình ảnh hoặc âm thanh cho câu hỏi ở bên dưới nếu cần.</span>
              </div>
              <button 
                onClick={handleExtract}
                disabled={isExtracting || !rawInput.trim()}
                className="bg-gradient-to-br from-[#2b8cee] to-blue-400 hover:scale-[1.02] active:scale-[0.98] text-white px-8 py-3 rounded-xl font-bold text-base flex items-center gap-3 transition-all shadow-lg shadow-[#2b8cee]/30 disabled:opacity-60 disabled:cursor-not-allowed disabled:hover:scale-100"
              >
                <Wand2 size={20} className={isExtracting ? 'animate-spin' : ''} />
                {isExtracting ? 'Đang trích xuất...' : 'Trích xuất với AI'}
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Layout chính: Form và Sidebar */}
      <div className="flex flex-col lg:flex-row gap-8">
        <main className="flex-1 flex flex-col gap-6">
          
          {/* Card Tệp đính kèm */}
          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-4">
            <h3 className="text-sm font-bold uppercase tracking-wider text-gray-500">Tài liệu đính kèm (Tùy chọn)</h3>
            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
              <div className="group relative flex flex-col items-center justify-center gap-3 rounded-lg border-2 border-dashed border-gray-200 hover:border-[#2b8cee]/80 hover:bg-[#2b8cee]/5 bg-gray-50/50 p-6 transition-all cursor-pointer">
                <div className="size-12 rounded-full bg-[#2b8cee]/10 flex items-center justify-center text-[#2b8cee]">
                  <ImageIcon size={24} />
                </div>
                <div className="text-center">
                  <h3 className="text-sm font-bold text-gray-800">Tải ảnh lên</h3>
                  <p className="text-xs text-gray-500 mt-1">JPG, PNG (Max 5MB)</p>
                </div>
                <input accept="image/*" className="absolute inset-0 opacity-0 cursor-pointer" type="file" />
              </div>
              <div className="group relative flex flex-col items-center justify-center gap-3 rounded-lg border-2 border-dashed border-gray-200 hover:border-[#2b8cee]/80 hover:bg-[#2b8cee]/5 bg-gray-50/50 p-6 transition-all cursor-pointer">
                <div className="size-12 rounded-full bg-[#2b8cee]/10 flex items-center justify-center text-[#2b8cee]">
                  <Mic size={24} />
                </div>
                <div className="text-center">
                  <h3 className="text-sm font-bold text-gray-800">Thêm âm thanh</h3>
                  <p className="text-xs text-gray-500 mt-1">MP3, WAV (Max 10MB)</p>
                </div>
                <input accept="audio/*" className="absolute inset-0 opacity-0 cursor-pointer" type="file" />
              </div>
            </div>
          </div>

          {/* Card Nội dung câu hỏi */}
          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-4">
            <label className="text-base font-bold flex items-center gap-3 text-gray-800">
              <FileEdit size={20} className="text-[#2b8cee]" />
              Nội dung câu hỏi
            </label>
            <textarea 
              value={questionText}
              onChange={(e) => setQuestionText(e.target.value)}
              className="w-full resize-none rounded-lg border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[120px] p-4 text-base transition-all"
              placeholder="AI sẽ điền nội dung câu hỏi đã được chuẩn hóa vào đây..."
            ></textarea>
          </div>

          {/* Card Danh sách đáp án */}
          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-5">
            <div className="flex justify-between items-center">
              <label className="text-base font-bold flex items-center gap-3 text-gray-800">
                <CheckSquare size={20} className="text-[#2b8cee]" />
                Các phương án trả lời
              </label>
              <span className="text-xs font-medium text-gray-500 bg-gray-100 px-3 py-1 rounded-full">AI sẽ tự động chọn đáp án đúng</span>
            </div>
            <div className="flex flex-col gap-4">
              {options.map((opt, i) => (
                <div key={i} className="flex items-center gap-4">
                  <input 
                    checked={correctIndex === i}
                    onChange={() => setCorrectIndex(i)}
                    className="size-5 border-2 border-gray-300 text-[#2b8cee] focus:ring-[#2b8cee]/50 cursor-pointer transition-all shrink-0 mt-0.5"
                    name="correct_answer" 
                    type="radio"
                  />
                  <div className="flex-1">
                    <input 
                      value={opt}
                      onChange={(e) => updateOption(i, e.target.value)}
                      className={`w-full rounded-lg border p-3.5 text-base transition-all ${correctIndex === i ? 'border-[#2b8cee] bg-[#2b8cee]/5 font-semibold shadow-inner' : 'border-gray-200 bg-gray-50'}`}
                      placeholder={`Nội dung đáp án ${String.fromCharCode(65 + i)}`}
                      type="text" 
                    />
                  </div>
                </div>
              ))}
            </div>
          </div>

          {/* Card Lời giải thích */}
          <div className="bg-white rounded-xl p-6 shadow-sm border border-gray-200 flex flex-col gap-4">
            <label className="text-base font-bold flex items-center gap-3 text-gray-800">
              <Lightbulb size={20} className="text-[#2b8cee]" />
              Giải thích chi tiết (do AI tạo)
            </label>
            <textarea 
              value={explanation}
              onChange={(e) => setExplanation(e.target.value)}
              className="w-full resize-none rounded-lg border border-gray-200 bg-gray-50 focus:border-[#2b8cee] focus:ring-2 focus:ring-[#2b8cee]/20 min-h-[140px] p-4 text-base transition-all"
              placeholder="AI sẽ điền lời giải thích chi tiết cho đáp án đúng vào đây..."
            ></textarea>
          </div>
        </main>

        {/* Sidebar được tách thành component riêng */}
        <CreateQuestionSidebar 
          difficulty={difficulty}
          setDifficulty={setDifficulty}
          topic={topic}
          setTopic={setTopic}
          tags={tags}
          setTags={setTags}
          onSave={handleSave}
        />
      </div>
    </div>
  );
};

export default CreateQuestionWithAIPage;
