'use client';

import React, { useState } from 'react';
import { BookOpen, BarChart2, Hash, Save, X, Plus } from 'lucide-react';
import { Difficulty, Topic } from '@/lib/definitions';

// Component tái sử dụng cho các ô thiết lập trong Sidebar
const SidebarCard: React.FC<{ title: string; icon: React.ReactNode; children: React.ReactNode }> = ({ title, icon, children }) => (
  <div className="bg-white rounded-2xl p-6 shadow-sm border border-gray-200">
    <div className="flex items-center gap-3 mb-4">
      <div className="text-[#2b8cee]">{icon}</div>
      <h3 className="text-base font-bold text-gray-800">{title}</h3>
    </div>
    <div className="space-y-4">{children}</div>
  </div>
);

// Component cho việc quản lý Tags
const TagInput: React.FC<{ tags: string[], setTags: (tags: string[]) => void }> = ({ tags, setTags }) => {
  const [inputValue, setInputValue] = useState('');

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === 'Enter' && inputValue.trim()) {
      e.preventDefault();
      if (!tags.includes(inputValue.trim())) {
        setTags([...tags, inputValue.trim()]);
      }
      setInputValue('');
    }
  };

  const removeTag = (tagToRemove: string) => {
    setTags(tags.filter(tag => tag !== tagToRemove));
  };

  return (
    <div>
      <div className="flex flex-wrap gap-2 mb-3">
        {tags.map(tag => (
          <div key={tag} className="flex items-center gap-1.5 bg-[#2b8cee]/10 text-[#2b8cee] text-xs font-semibold px-2.5 py-1 rounded-full">
            <span>{tag}</span>
            <button onClick={() => removeTag(tag)} className="hover:bg-black/10 rounded-full">
              <X size={14} />
            </button>
          </div>
        ))}
      </div>
      <input
        type="text"
        value={inputValue}
        onChange={(e) => setInputValue(e.target.value)}
        onKeyDown={handleKeyDown}
        placeholder="Thêm tag và nhấn Enter..."
        className="w-full px-3 py-2 bg-gray-50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-[#2b8cee]/50 focus:border-[#2b8cee] transition-colors text-sm"
      />
    </div>
  );
};

interface SidebarProps {
  difficulty: string;
  setDifficulty: (value: string) => void;
  topic: string;
  setTopic: (value: string) => void;
  tags: string[];
  setTags: (tags: string[]) => void;
  onSave: () => void;
}

const CreateQuestionSidebar: React.FC<SidebarProps> = ({ difficulty, setDifficulty, topic, setTopic, tags, setTags, onSave }) => {
  return (
    <aside className="flex-1 lg:max-w-sm w-full flex flex-col gap-6">
      <SidebarCard title="Phân loại & Cấu hình" icon={<BookOpen size={20} />}>
        <div>
            <label htmlFor="topic" className="text-sm font-medium text-gray-600 mb-1.5 block">Chủ đề</label>
            <select id="topic" value={topic} onChange={e => setTopic(e.target.value)} className="w-full p-2.5 bg-gray-50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-[#2b8cee]/50 focus:border-[#2b8cee] transition-colors cursor-pointer">
                {Object.values(Topic).map(topicName => (
                    <option key={topicName} value={topicName}>{topicName}</option>
                ))}
            </select>
        </div>
        <div>
            <label htmlFor="difficulty" className="text-sm font-medium text-gray-600 mb-1.5 block">Độ khó</label>
             <select id="difficulty" value={difficulty} onChange={e => setDifficulty(e.target.value)} className="w-full p-2.5 bg-gray-50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-[#2b8cee]/50 focus:border-[#2b8cee] transition-colors cursor-pointer">
                {Object.values(Difficulty).map(level => (
                    <option key={level} value={level}>{level}</option>
                ))}
            </select>
        </div>
      </SidebarCard>

      <SidebarCard title="Tags (Từ khóa)" icon={<Hash size={20} />}>
        <TagInput tags={tags} setTags={setTags} />
      </SidebarCard>

       <div className="bg-white rounded-2xl p-6 shadow-sm border border-gray-200 flex flex-col gap-3">
           <button onClick={onSave} className="w-full bg-[#2b8cee] hover:bg-[#2b8cee]/90 text-white px-6 py-3 rounded-lg font-bold flex items-center justify-center gap-2 transition-all shadow-md shadow-[#2b8cee]/30 hover:scale-[1.02] active:scale-[0.98]">
                <Save size={18} />
                Lưu câu hỏi
            </button>
            <button className="w-full bg-gray-100 hover:bg-gray-200 text-gray-700 px-6 py-3 rounded-lg font-bold flex items-center justify-center gap-2 transition-all active:scale-98">
                Hủy
            </button>
       </div>
    </aside>
  );
};

export default CreateQuestionSidebar;
